import { Component, OnInit } from '@angular/core';

import { Video } from 'src/app/services/videos/video';
import { SidebarService } from 'src/app/services/sidebar/sidebar.service';
import { PhotoLibraryService } from 'src/app/services/photo-library/photo-library.service';
import { ImagesService } from 'src/app/services/images/images.service';
import { VideosService } from 'src/app/services/videos/videos.service';
import { contentTypes } from 'src/app/constants/content-types';

@Component({
  selector: 'app-mediagallery-page',
  templateUrl: './mediagallery-page.component.html',
  styleUrls: ['./mediagallery-page.component.scss']
})
export class MediagalleryPageComponent implements OnInit {

  public albumsTotal: number = null;
  public videosTotal: number = null;
  public photoUrl: string = null;
  public video: Video = null;

  constructor(
    private sidebarService: SidebarService,
    private photoLibraryService: PhotoLibraryService,
    private imagesService: ImagesService,
    private videosService: VideosService
  ) {
    this.sidebarService.handleSetSettings({ showSidebar: false, bgColor: '#fff' });
  }

  ngOnInit() {
    this.photoLibraryService.getItems({ filter: `ContentType eq '${encodeURI(contentTypes.album)}'`, top: 5000 })
      .subscribe((items: any[]) => this.albumsTotal = items.length);
    this.photoLibraryService.getRandomItems({top: 1, filter: `ContentType eq '${encodeURI(contentTypes.photo)}'` })
      .subscribe(photos => {
        if (photos.length > 0) {
          this.photoUrl = photos[0].url;
        }
      });
    this.videosService.getItems().subscribe((items: any[]) => {
      this.videosTotal = items.length;
      if (items.length > 0) {
        const i = Math.floor(Math.random() * items.length);
        this.video = items[i];
      }
    });
  }

  getEnding(number: number, endings: string[]) {
    let ending: string;
    number = number % 100;
    if (number >= 11 && number <= 19) {
      ending = endings[2];
    } else {
      const i = number % 10;
      switch (i) {
        case (1): ending = endings[0]; break;
        case (2):
        case (3):
        case (4): ending = endings[1]; break;
        default: ending = endings[2];
      }
    }
    return ending;
  }

  public prepareLink(link: string) {
    return this.imagesService.prepareBackgroundUrl(link);
  }
}
