import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { MediagalleryPageComponent } from './components/mediagallery-page/mediagallery-page.component';

const routes: Routes = [
  {
    path: '',
    component: MediagalleryPageComponent,
    data: { animation: 'fadeAnimation', title: 'Медиагалерея' }
  },
  {
    path: 'photo',
    loadChildren: '../photogallery/photogallery.module#PhotogalleryModule'
  },
  {
    path: 'video',
    loadChildren: '../videogallery/videogallery.module#VideogalleryModule'
  },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class MediagalleryRoutingModule { }
